IntToByteArray = function(int) {
    var byteArray = [0, 0, 0, 0];
    for ( var index = 0; index < 4; index ++ ) {
        var byte = int & 0xff;
        byteArray [ (3 - index) ] = byte;
        int = (int - byte) / 256 ;
    }
    return byteArray;
};

function get_pin (byte) {
  if (byte < 6) return 1;
  else {
    return (byte % 5) + 1;
  }
}

